<template>
  <div>
    <div id="userStatus">
      {{user.uname}}
      <!-- {{this.login[0].uname}} -->
      <button type='button' @click="logout">로그아웃</button>
    </div>

    <div class="modal" v-show="isLogin">
      <div class ="overlay"></div>
      <div class = "modal-card">
        <h3 id="loginTitle"> Login </h3>
        <div id="loginContent">
          <input type="text" v-model="user.uid" placeholder="아이디" class="input">
          <input type="text" v-model="user.password" placeholder="비번" class="input" @keyup.enter="signin">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  created(){
    var key = localStorage.getItem('access_id');
    console.log("the key name is "+key);
    if(key == undefined){
      this.isLogin = true;
    }
    else{
      this.user.uname = key;
    }
  },
  data () {
    return {
      user: {
        uid:"",
        password:"",
        uname:""
      },
      login:[],
      isLogin: false,
    }
  },
  methods:{
    signin(){
      this.$http.post('/api/signin',{
        params:{
          uid : this.user.uid,
          password : this.user.password
        }
      }).then((res) => {
        this.login = res.data;
        this.user.uname = res.data[0].uname;
        localStorage.setItem("access_id", this.login[0].uname);
        this.isLogin = false;
      }).catch((err) => {
        console.log(err);
      })
    },
    logout(){
      this.user.uname = '';
      localStorage.clear();
      this.isLogin = true;
      console.log(this.isLogin);
    }
  }
}
</script>

