<template>
        <div id="mainDiv">
            <div id="center">
                <div>
                    <h2>지출등록</h2>
                    <input type="text" v-model="expends.elname" placeholder="세부항목이름" class="input">
                    <input type="text" v-model="expends.value" placeholder="사용금액" class="input" @keyup.enter="insertExpen">
                </div>

                <div>
                    <table>
                        <tr><td>이름</td><td>금액</td><td>시간</td></tr>

                        <tr v-for="t in expendsList" v-bind:key="t.elid">
                            <td>{{t.elname}}</td><td>{{t.value}}</td><td>{{t.dates}}</td>
                        </tr>

                    </table>
                </div>
            </div>

            
        </div>
</template>

<script>

export default ({
    data(){
        return{
            expends:{
                elname:"",
                value:""
            },
            expendsList:[],
        }
    },
    created: function(){
        this.getExpenList();
    },
    methods:{
        insertExpen(){
            this.$http.post('/api/insertExpen', {
                param : this.expends
            }).then((res)=>{
                console.log(res);
                this.inputClear();
                this.getExpenList();
            })
        },
        inputClear(){
            this.expends.elname = ''
            this.expends.value = ''
        },
        getExpenList(){
            this.$http.post('/api/getExpenList')
                .then((res) => {
                    this.expendsList = res.data;
                }
            )
        },

    }
})
</script>

