<template>
        <div id='mainDiv'>
            <div id='center'>
                <div id='income'>
                    <table>
                        <tr><td>이름</td><td>금액</td></tr>

                        <tr v-for="t in offerList" v-bind:key="t.i">
                            <td>{{t.offerType}}</td><td>{{t.amount}}</td>
                        </tr>
                    </table>
                </div>
                <div id='expenditure'>
                    <table>
                        <tr><td>이름</td><td>금액</td></tr>

                        <tr v-for="t in expendsList" v-bind:key="t.elid">
                            <td>{{t.elname}}</td><td>{{t.value}}</td>
                        </tr>
                    </table>
                </div>
                <div id='result'>
                    <p>수입합계 : {{sumofoffering}}</p> 
                    <p>지출합계 : {{sumofoexpen}}</p>
                </div>
            </div>
        </div>
</template>

<script>

export default ({
    data(){
        return{
            expendsList:[],
            offerList:[],
            sumofoffering:0,
            sumofoexpen:0,
            len:0
        }
    },
    created: function(){
        
        this.$http.post('/api/getOfferList')
            .then((res) => {
                this.offerList = res.data;
            });

        this.$http.post('/api/getExpen')
            .then((res) => {
                this.expendsList = res.data;
            });
    },
    watch:{
        offerList: function(){
            this.offerList.forEach((data) => {
                this.sumofoffering = this.sumofoffering + data.amount;
            });
        },
        expendsList: function(){
            this.expendsList.forEach((data) => {
                this.sumofoexpen = this.sumofoexpen + data.value;
            });
        }
    }
})
</script>


