<template>
    <div id='mainDiv'>
        <div id='center'>
            <div id='addOfferTypeDiv'>
                <h3>헌금종류추가</h3>

                <input id="textinput" type="text" v-model="offerType" placeholder="새로운 헌금 추가" class="input" @keyup.enter="addOfferType"/>

                <div @click="offerTableVisibility = true, offerTableUnvisibility=false" v-if='offerTableUnvisibility'>더보기▼</div>
                <div @click="offerTableVisibility = false, offerTableUnvisibility=true" v-if='offerTableVisibility'>감추기▲</div>

                <div v-if='offerTableVisibility'>
                    <table id='testTable'>
                        <tr v-for="s in offerTypeList" v-bind:key="s.offerTeg">
                            <td tpye='button' @click="editOfferType(s.offerType)">{{s.offerType}}</td>
                        </tr>
                    </table>
                </div>
            </div>

            <div id='addExepnDiv'>
                <div>
                    <h2>Add Root</h2>
                    <input type="text" v-model="ername" placeholder="항목이름" class="input" @keyup.enter="insertRoot">
                </div>

                <div>
                    <h2>Add Leaf</h2>
                    <input type="text" v-model="expendleaf.ername" placeholder="항목이름" class="input">
                    <input type="text" v-model="expendleaf.elname" placeholder="세부항목이름" class="input" @keyup.enter="insertLeaf">
                </div>
            </div>

            <div id='addMember'>
                <h3>성도추가</h3>
                <input type="text" v-model="memberName" placeholder="이름" class='input' @keyup.enter="addMember">

                <div @click="memberTableVisibility = true, memberTableUnvisibility=false" v-if='memberTableUnvisibility'>더보기▼</div>
                <div @click="memberTableVisibility = false, memberTableUnvisibility=true" v-if='memberTableVisibility'>감추기▲</div>

                <table id='rightTable' v-if='memberTableVisibility'>
                    <tr v-for="t in memberList" v-bind:key="t.mname">
                        <td class='rightTableTd'>
                            {{t.mname}}
                        </td>
                    </tr>
                </table>
            </div>

            <div id="addQuickOption">
                <h2>퀵버튼 추가</h2>
            </div>
        </div>
    </div>
</template>

<script>

export default ({
    data(){
        return {
            offerType:"",
            offerTypeList: [],
            offerTableVisibility : false,
            offerTableUnvisibility : true,
            memberTableVisibility : false,
            memberTableUnvisibility : true,
            expendleaf:{
                ername:"",
                elname:"",
            },
            ername : "",
            memberName : "",
            memberList: [],
        }
    },
    methods:{
        addOfferType(){
            this.$http.post('/api/addOfferType',{
                param: this.offerType
            }).then((res) => {
                console.log(res)
                this.inputClear();
                this.getOfferTypes();
            }).catch((err) => {
                console.log(err);
            })
        },
        getOfferTypes(){
            this.$http.post('/api/getOfferTypes')
            .then((res2) =>{
                this.offerTypeList = res2.data;
            })
        },
        inputClear(){
            this.offerType = ''
            this.expendleaf.ername = ''
            this.expendleaf.elname = ''
            this.ername = ''
        },
        insertRoot(){
            this.$http.post('/api/insertRoot', {
                param : this.ername
            }).then((res)=>{
                console.log(res);
                this.inputClear();
            })
        },
        insertLeaf(){
            this.$http.post('/api/insertLeaf', {
                param : this.expendleaf
            }).then((res)=>{
                console.log(res);
                this.inputClear();
            })
        },
        addMember(){
            this.$http.post('/api/addMember',{
                params:{
                    mname : this.memberName
                }
            }).then(() =>{
                this.getMember();
                this.member.mname = '';
            })
        },
        getMemberList(){
            this.$http.post('/api/getMemberList')
                .then((res)=>{
                    this.memberList = res.data;
                })
        },
    },
    created: function(){
        this.getOfferTypes();
        this.getMemberList();
    },
})
</script>

