<template>
  <center id="mainDiv">
    <Modal v-if='modalOn'
           v-bind:msg="msg"
           v-bind:ModalData='modalItem'
           v-bind:type='modalType'
          @close="modalOn = false"/>

    <div id="center">
      <div class='input-div'>
        <div class="offeringInput">
        <!-- v-model input에서 값을 받아올 이름 지정 --> 
          <input type="text" v-model="offering.mname" placeholder="이름" class="input">
          <input type="text" v-model="offering.offerteg" placeholder="헌금종류" class="input">
          <input type="text" v-model="offering.amount" placeholder="금액" class="input" @keyup.enter="addOffering">
        </div>

        <div class='offeringButton'>
          <div class="quick">
            <button @click="editOfferType('십일조')">십일조</button>  __ 
            <button @click="editOfferType('감사헌금')">감사헌금</button>
          </div>

          <div class="quick">
            <button @click="editAmount(10000)">10000</button>  __ 
            <button @click="editAmount(30000)">30000</button>  __ 
            <button @click="editAmount(50000)">50000</button>
          </div>

          <div @click="addOffering" class="quickButton"> 추가 </div>
        </div>

      </div>

      <table class="userTable">
        <tr><th>이름</th><th>헌금</th><th>금액</th><th>날짜</th></tr>

        <tr v-for="t in offeringList" v-bind:key="t.name" class="tableTr">
          <td>{{t.mname}}</td><td>{{t.offertype}}</td>
          <td>{{t.amount}}</td><td>{{t.dates}}</td>
          <td @click="onModal(1, t)" class='modal-bnt'>수정</td>
          <td @click="onModal(2, t)" class='modal-bnt'>삭제</td>
        </tr>
      </table>
    </div> 
  </center>
</template>

<script>
import Modal from "../components/Modal.vue"

export default {
  components : {Modal},
  data () {
    return {
      offering:{
        mname:'',
        offerteg:'',
        amount:'',
      },
      offeringList: [],
      modalOn: false,
      msg:"",
      modalItem:"",
      modalType:''
    }
  },
  methods:{
    addOffering(){
      this.$http.post('/api/addOffering',{
        offering: this.offering
      }).then((res) => {
          console.log(res)
          this.getOfferingList();
          this.inputClear();
        }).catch((err) => {
          console.log(err);
        }
      )
    },
    inputClear(){
      this.offering.mname = '';
      this.offering.offerteg = '';
      this.offering.amount = '';
    },
    getOfferingList(){
      this.$http.post('/api/getOfferingList')
        .then((res) => {
          this.offeringList = res.data
        })
    },
    editAmount(n){this.offering.amount = n;},
    editOfferType(n){this.offering.offerteg = n;},
    
    editOffering(){
      this.onModal();
      this.msg = '수정입니다.'
    },
    onModal(n, item){
      this.modalItem = item
      this.modalType = n;
      this.modalOn = true
    }
  },
  created: function(){
    this.getOfferingList();
    this.getOfferTypes();
  },
}
</script>

<style>
.userTable {
  background: #eff0ec;
}
.quickButton {
  width: 50px;
  background: white;
  height: 20px;
  margin: 10px;
}
.tableTr {
  background: #2d2d2d;
  transition: 0.7s;
  color: #eff0ec;
}

.tableTr:hover {
  background: #f2ebab;
  color: #2d2d2d;
}
.offeringInput{
  display: flex;
  flex-direction: column;
  background: red;
  justify-content: flex-start;
}
.input-div{
  display: flex;
}
.offeringButton{
  position: relative;
  left:200px;
  margin:
  20px;
}

.offeringButton .quick{
  margin: 20px;
}
.offeringInput{
  position: relative;
  left: 100px;
}


</style>