import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Offer from '../views/Offer.vue'
import Member from '../views/Member.vue'
import Expenditure from '../views/Expenditure.vue'
import Closing from '../views/Closing.vue'
import Option from '../views/Option.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      title: "Home"
    }
  },
  {
    path: '/offer',
    name: 'Offer',
    component: Offer
  },
  {
    path: '/member',
    name: 'Member',
    component: Member
  },
  {
    path: '/expenditure',
    name: 'Expenditure',
    component: Expenditure
  },
  {
    path: '/closing',
    name: 'Closing',
    component: Closing
  },
  {
    path: '/option',
    name: 'Option',
    component: Option
  }

]

const router = new VueRouter({
  routes
})

export default router
